* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  .main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
  }
  .main-all {
    box-shadow: 0px 0px 12px #ccc;
    border-radius: 8px;
    padding: 1rem;
  }
  input {
    width: 100%;
    font-size: 1.2rem;
    padding: 8px 4px;
    margin: 8px 0;
    border-radius: 8px;
  }
  
  .btn {
    display: block;
    width: 100%;
    font-size: 1.2rem;
    margin: 8px 0;
    padding: 10px 0;
    background-color: #333;
    color: #fff;
    border: 1px solid #333;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .btn-primary {
    background-color: #fff;
    color: #333;
  }
  
  .head {
    text-align: center;
    margin: 24px 0;
  }
  p {
    margin: 10px 0;
  }
  .end {
    text-align: center;
}
.end p {
    font-size: 20px;
    color: chocolate;
    font-weight: 700;
}